import { Component, AfterViewInit, OnInit } from '@angular/core';
import { NavController } from 'ionic-angular';
import { CapacitorVideoRecorder } from 'capacitor-video-recorder';
@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage implements AfterViewInit, OnInit {
  recorder: CapacitorVideoRecorder;
  timer;
  duration: number;
  recording: boolean;
  constructor(public navCtrl: NavController) {
    this.recorder = new CapacitorVideoRecorder();
  }
  ngOnInit() {
    this.recorder.initialize();
  }
  ngAfterViewInit() {
    this.recorder.show({ position: 1 });
  }
  startRecording() {
    this.recorder.startRecording();
    this.recording = true;
    this.timer = setInterval(async () => {
      const duration = await this.recorder.getDuration();
      this.duration = duration.value;
    });
  }
  stopRecording() {
    this.recorder.stopRecording();
    this.recording = false;
    clearInterval(this.timer);
  }

  toggleCamera(){
    this.recorder.toggleCamera();
  }
}



